            <p>Throughout my various roles on AXIS, I was involved in feature and roadmap prioritisation, from early contribution as UX lead to ultimately facilitating and driving prioritisation workshops quarterly. Weekly meetings were also run to evaluate changing priorities from Commercial and service teams, new customer requests via Canny and progress of current features in development.</p>
