              <li>mtribes is a novel product in its approach, yet it still exists in a wider ecosystem of more established and successful competitors. Many of these competitors had already achieved economies of scale and their pricing would benefit. This required us to formulate very clear ROI use cases and examples that potential customers could connect to. It was also vital that customers could realise the value of mtribes as quickly as possible during a trial period and so there was a large focus on onboarding, CLI tools for developers, quick start guides and interoperability.</li>
